import React, { Component } from "react";

import { withRouter } from "react-router-dom";
import Footer from "../Footer";

const html = '<main role="main"> <section class="container-fluid" style="position: relative; height: 425px; background-image: linear-gradient(var(--red), var(--orange));"> <div class="row justify-content-center" style="height: 100%"> <h2 class="col-12 text-center align-self-center align-middle text-white">Conditions Générales de Vente</h2> <svg style="position: absolute; left:0; bottom: 0; width: 100%; height: 50px;" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1416.99 174.01" preserveAspectRatio="none"> <path style="" fill="#fafafa" d="M0,280.8S283.66,59,608.94,163.56s437.93,150.57,808,10.34V309.54H0V280.8Z" transform="translate(0 -135.53)"/></svg> </div></section> <section class="container"> <div class="row"> <div class="col-12"> <p>Conditions Générales de Vente OPTION PUBLIC pour les entreprises :<br>OPTION PUBLIC<br>Association déclarée<br>Siège social : 1 B Route de Béthune, 62300 Lens<br>SIRET : 82274487600027<br>Email : <a href="/#/contact">covilience.org/contact</a><br><br>Mis à jour le 01 mai 2020<br><br>1. Application des présentes conditions générales de vente<br><br>Les présentes conditions générales de vente « BtoB » (ci-après les « CGV ») s’appliquent pour tout achat effectué par un professionnel, personne physique ou moral (ci-après le « Client ») sur le site « <a href="/">covilience.org</a> » (ci-après le « Site »). Le fait que OPTION PUBLIC ne se prévale pas, à un moment donné, de l’une quelconque des présentes CGV ne peut être interprété comme valant renonciation à se prévaloir ultérieurement de l’une quelconque des dites conditions.<br><br>Le Site et la marque OPTION PUBLIC appartiennent à l\'association OPTION PUBLIC sus désignée (ci-après le « Vendeur »).<br><br>La Société peut modifier sans préavis les présentes CGV à tout moment en postant sur le Site la dernière version à jour. Dans ce cas, les CGV applicables seront celles en vigueur à la date de la commande par le Client. Le Client peut demander toute version antérieure des CGV par courrier postal à l’adresse du siège social de la Société ou par courrier électronique à l’adresse suivante : « <a href="/#/contact">covilience.org/contact</a> ».<br><br>Le Client déclare avoir pris connaissance des CGV et les avoir acceptées, en cliquant sur la case correspondante, avant la passation de toute commande sur le Site. Les présentes CGV prévaudront sur toutes autres conditions générales ou particulières non expressément agréées préalablement à la commande par le Vendeur.<br><br>2. Spécification technique<br><br>Les équipements et les frais de télécommunication nécessaires à l’accès et à l’utilisation du Site sont à la charge du Client.<br><br>3. Création de compte sur le Site<br><br>Avant la passation de toute commande sur le Site, le Client doit ouvrir un compte. Il déclare que les informations renseignées, dont il est seul responsable, sont exactes et complètes et ne portent pas atteinte, à quel que titre que ce soit, aux droits de tiers, quels qu’ils soient.<br><br>4. Utilisation du compte par le Client<br><br>Pour toute commande sur le site, il appartient au Client d’assurer la confidentialité des identifiants renseignés. Toute connexion au compte d’un Client à partir des identifiants sera présumée avoir été faite par le Client lui‐même. Le Client est seul responsable de l\'utilisation de ses identifiants par des tiers ou des actions ou déclarations faites par l\'intermédiaire de son compte, qu\'elles soient frauduleuses ou non.<br><br>5. Caractéristique des produits<br><br>Les principales caractéristiques des produits sont présentées sur le catalogue de produits ou sur le Site sur les fiches d’information de chacun des produits, en fonction des informations détenues par le Vendeur auprès de ses fournisseurs. Le Vendeur ne peut être reconnu responsable des informations erronées de ses fournisseurs.<br><br>Les reproductions photographiques des produits sur le catalogue ou sur le Site ne sont pas contractuelles et sont communiquées à titre d’illustration, de sorte qu’elles ne peuvent pas engager la responsabilité du Vendeur ni remettre en cause une commande confirmée.<br><br>Le Client sélectionne, selon ses besoins, les produits et les quantités sur le Site, dont le récapitulatif s’inscrit dans le bouton « Panier ». Conformément à l’article 1127-2 du Code civil, le Client pourra visualiser le détail de sa commande (y compris la quantité, les caractéristiques et les références des produits commandés, l’adresse de facturation et de livraison, le moyen de paiement et le prix et le délai de livraison), corriger d’éventuelles erreurs, avant de confirmer celle-ci et exprimer son acceptation des présentes CGV. Le client valide sa commande en sélectionnant un type de règlement proposé sur le Site.<br><br>Il accepte alors sans réserve le processus de commande et les présentes conditions de vente. Le Client reçoit ensuite un courrier électronique automatique de confirmation de commande.<br><br>Les commandes sont définitivement validées après réception du règlement par le Vendeur. Pour les règlements par chèque, la commande est définitivement validée après encaissement du chèque.<br><br>Aucune commande ne peut être modifiée à compter de la validation de celle-ci par le Vendeur sans son accord expresse.<br><br>En cas d’impossibilité technique d’honorer la commande du Client, le Vendeur se réserve le droit de proposer un autre produit de qualité ou de caractéristiques techniques similaires ou supérieures à celles du produit commandé par le Client. Dans ce cas, la Société s’efforcera de prévenir le Client et de lui faire parvenir une nouvelle offre tenant compte de ces modifications.<br><br>6. Prix<br><br>Les prix affichés sur le Site sont exprimé en TTC, diminué d’éventuelles remises accordées au Client, selon les modalités définies sur le Site. Par voie de conséquence, le récapitulatif de commande et la facture font apparaître les prix hors taxes, la majoration du taux de TVA et le prix TTC, outre les frais de transport applicables au jour de la commande.<br><br>Ces tarifs présents sur le Site peuvent être modifiés à tout moment, sans avertissement et, notamment, en cas de changement des données fiscales ou économiques. Les factures sont établies conformément aux prix s’affichant sur le Site au jour de la commande.<br><br>7. Paiement – Modalités<br><br>Toute commande effectuée sur le Site est payable comptant sans escompte. Pour les règlements par chèque ou virement, toute somme non payée dans le délai maximum de 10 jours à compter de la date de la commande donnera lieu, de plein droit et sans mise en demeure préalable, au paiement (i) d’une pénalité de retard dont le taux d’intérêt correspondra au taux d’intérêt appliqué par la banque centrale européenne à son opération de refinancement la plus récente majoré de 10 points de pourcentage – ces intérêts courront du lendemain de l’échéance jusqu’au paiement complet – et (ii) donnera également lieu, de plein droit et sans notification préalable, au paiement d’une indemnité forfaitaire légale pour frais de recouvrement, d’un montant de 40 euros. Si les frais de recouvrement exposés sont supérieurs au montant de cette indemnité forfaitaire, le Vendeur se réserve le droit de réclamer une indemnité complémentaire.<br><br>Toute somme non payée dans le délai maximum de 10 jours à compter de la date de la commande ouvrira le droit au Vendeur de résilier la vente. Si la banque ou Paypal refuse de débiter une carte, la commande sera annulée et la vente résolue de plein droit. Le Client en sera informé par courrier électronique.<br><br>Les moyens de paiement suivants sont mis à la disposition du Client pour régler ses commandes :<br><br>- carte bancaire (Carte Bleue, Visa, MasterCard) paiement en ligne « Stripe »,<br><br>- virement Bancaire par avance.<br><br>Le Vendeur prendra toutes les mesures nécessaires pour garantir la sécurité et la confidentialité des données bancaires transmises sur le Site dans le cadre du paiement en ligne.<br><br>Toutes les informations relatives au paiement fournies sur le Site ne sont pas traitées par le Vendeur. Les données relatives au règlement de la commande sont donc traitées directement par la banque du Client dans un environnement sécurisé.<br><br>8. Offre et promotion sur le Site<br><br>Sauf mention particulière sur le Site, toute offre ou promotion présentée sur le Site conduisant à une réduction de prix ou à un produit complémentaire offert n’est applicable qu’une seule fois par Client au cours de la période de l’offre ou de la promotion.<br><br>9. Accusé de réception et facturation<br><br>Un accusé‐réception de la commande est automatiquement adressé au Client par courrier électronique, à condition que l’adresse électronique communiquée par le biais du formulaire d’inscription du Client soit correcte.<br><br>Aucune confirmation de commande par courrier postal ou par télécopie n’est envoyée par le Vendeur. Le Client accepte la transmission par le Vendeur de toute facture dématérialisée par voie électronique.<br><br>10. Frais de Port et de Préparation<br><br>Les frais de port sont offerts pour toute commande unitaire d’un montant égal ou supérieur à 500 euros T.T.C.<br><br>Pour une commande inférieure à ce montant, une participation forfaitaire de 10,00 euros T.T.C. est à la charge du Client pour toute livraison en France métropolitaine, 20,00 euros T.T.C. pour l\'Europe et 30,00 euros T.T.C. pour les autres destinations du monde, couvrant une partie des frais fixes liés à la préparation et la livraison de la commande.<br>Certaines commandes nécessitant des opérations logistiques spéciales, contenant des produits de dimensions ou de poids particulièrement importants, pourront faire l’objet de frais de ports spécifiques.<br><br>11. Délais de Livraison<br><br>La livraison des marchandises aura lieu après réception du paiement par le Vendeur. Les livraisons sont préparées et réalisées uniquement les jours ouvrables.<br><br>Les délais de livraison indiqués dans notre catalogue, site internet, devis, facture, bien que déterminés le plus exactement possible ne constituent pas un engagement de notre part.<br><br>12. Vérification de la livraison et réclamation<br><br>Les marchandises voyagent aux risques et périls du Client auquel il appartient de vérifier le bon état au moment de la livraison.<br><br>A réception des produits, il est de la responsabilité du Client de remplir les principales obligations suivantes :<br><br>a) se faire présenter par le transporteur les documents de livraison ;<br><br>b) vérifier le nombre de colis et l’état des produits (vérification extérieure et vérification du contenu des colis). Le cas échéant, le Client doit formuler sur le bon de livraison du transporteur que celui-ci n’a pas assisté au déballage du ou des colis ;<br><br>c) accepter la livraison dès lors que les produits sont conformes à la commande et ce, même en cas de dommages, en signant lisiblement le bon de livraison ou le récépissé de transport et en y ajoutant le cachet de son établissement et le nom de la personne ayant réceptionné la livraison pour le compte du Client ;<br><br>d) en cas de dommages, formuler des réserves précises sur le récépissé original du transporteur. Sont non recevables des réserves qui ne sont pas significatives, claires et complètes et qui ne désignent pas les produits détériorés. Il est nécessaire de dater et signer les réserves ;<br><br>e) sous trois jours ouvrables, confirmer ces réserves par lettre recommandée adressée au transporteur ;<br><br>Sans préjudice des dispositions à prendre vis-à-vis du transporteur, les réclamations sur les vices apparents ou la non-conformité des produits livrés devront être formulées par courrier électronique, télécopie, téléphone ou courrier postal auprès du Vendeur, dans les sept (7) jours de la livraison desdits produits.<br><br>Tout retour devra préalablement faire l’objet d’un accord formel du Vendeur.<br><br>En l’absence de réserve notifiée au transporteur et/ou au Vendeur dans les conditions précitées, les produits seront réputés acceptés par le Client et aucune réclamation afférente à leur état ne pourra être acceptée a posteriori.<br><br>Toute réclamation qui nous serait adressée et qui ne respecterait pas cette procédure empêcherait le remplacement ou l’établissement de l’avoir correspondant aux produits détériorés ou manquants.<br><br>13. Accessibilité<br><br>Les produits sont livrés à tout lieu sur le territoire métropolitain français désigné par le Client lors du processus de commande par camion ou tout moyen de transport équivalent. En conséquence, les produits sont réputés mis à la disposition du Client à la date du chargement. Si cette livraison est retardée pour une raison indépendante de la volonté du Vendeur, elle sera réputée avoir été effectuée à la date convenue pour le chargement.<br><br>L’adresse de livraison doit être exacte et mentionner toute précision nécessaire. Il est de la responsabilité du Client de s’assurer des conditions d’accessibilité du lieu de livraison et d’informer le Vendeur ou le transporteur de toute spécificité d’accessibilité liée à la livraison.<br><br>En cas de problème d’accessibilité, les frais supplémentaires induits (second passage du transporteur, etc.) seront intégralement facturés au Client.<br><br>14. Livraisons Partielles<br><br>Sauf mention particulière portée sur le bon de commande, le Vendeur procédera à l’expédition immédiate des articles constituant des lots homogènes, utilisables et disponibles dans ses dépôts ; les soldes de commande feront l’objet de livraisons complémentaires ultérieures. Tous les produits du Site ou du catalogue sont proposés sous réserve du stock disponible. Le Vendeur informera par courrier électronique le Client en cas de rupture de stock.<br><br>15. Clause de Réserve de Propriété<br><br>Le Vendeur conserve la propriété des marchandises vendues jusqu’au paiement effectif de l’intégralité du prix. Ces dispositions ne font pas obstacle au transfert au Client des risques de perte et de détérioration des produits vendues dès leur chargement auprès du transporteur.<br><br>16. Garanties<br><br>La garantie est strictement limitée à la remise en état ou au remplacement des produits affectés d’un vice apparent ou d’un défaut de conformité, à l’exclusion de tout dédommagement, à quelque titre que ce soit. Les différences de dimensions, couleurs et poids des matériaux soumis à variation en raison de leur nature ou de leur mode de fabrication ne font l’objet d’aucune garantie.<br><br>17. Commentaire sur le site<br><br>Un espace de commentaire sur les produits est à la disposition du Client.<br><br>Conformément à l’article 52 de la loi n°2016-1321 du 7 octobre 2016 pour une République numérique, il est précisé que le Vendeur ne procède à aucune modération de ce contenu à quelque titre que ce soit.<br><br>Le Client s’engage à n’écrire qu’un commentaire par produit et veille à ce que son commentaire ne comporte pas :<br><br>- de lien hypertexte dirigeant vers un autre site que celui du Vendeur,<br><br>- de propos diffamatoires concernant le produit commenté, le Vendeur ou ses fournisseurs,<br><br>- de propos contraires aux bonnes mœurs et de manière générale, contraire à la loi.<br><br>Tout Client ou visiteur du site peut informer le Vendeur de l’existence de commentaires contraire au présent article par courrier électronique à l’adresse suivante : « <a href="/#/contact">covilience.org/contact</a> ». Le Vendeur se réserve le droit de supprimer tout commentaire ne respectant pas ses règles.<br><br>18. Réclamations<br><br>Toute réclamation concernant tant les quantités, les produits commandées que l’état des produits doit, pour être recevable, être adressée dans un délai de sept (7) jours calendaires suivant la réception des marchandises, auprès du Vendeur.<br><br>19 - Force majeure<br><br>Tout évènement indépendant de la volonté du Vendeur en raison d’un cas de force majeure, au sens de l’article 1218 du Code civil (tels que, sans que cette liste soit limitative, grèves, inondations, incendies, perturbations ou interruptions des transports, difficultés d’approvisionnement en matières premières ou en énergie), suspend l’exécution de ses obligations en cause pendant toute la durée de l’évènement de force majeure.<br><br>20 - Résiliation<br><br>Sans préjudice de toute réparation qui pourrait être obtenue ou réclamée, en cas de non-respect par le Client de l’une quelconque de ses obligations, en particulier, en cas de défaut de paiement, ainsi qu’en cas d’agissements du Client de manière à porter atteinte à la notoriété et/ou à l’image de marque du Vendeur, celui-ci sera en droit de résilier tout ou partie de la commande. Cette résiliation prendra effet huit (8) jours calendaires après mise en demeure, par lettre recommandée avec accusé de réception, restée infructueuse.<br><br>21. Fonctionnement du Site<br><br>Le Vendeur exclut sa responsabilité pour tout dommage résultant d’une interruption, d’un dysfonctionnement quel qu’il soit, d’une suspension ou de l’impossibilité d’accès au Site, et ce pour quelque raison que ce soit notamment en raison d’un dysfonctionnement technique, coupure de réseau ou électrique et tout cas de force majeure entrainant une discontinuité du service ainsi que pour tout dommage matériel ou immatériel qui résulterait d’une quelconque façon de la connexion au site ou de tout autre site relié par un hyperlien.<br><br>22. Archivage informatique<br><br>Tout contrat conclu avec le Client sera archivé par le Vendeur pendant une durée minimum de cinq (5) ans, conformément à l’article L.110‐4 du Code de commerce.<br><br>Le Client reconnaît que la procédure de commande et les présentes CGV sont conclues en ligne et constituent un « contrat électronique » entre le Client et le Vendeur, au sens des articles 1125 et suivants du Code civil.<br><br>Le Client accepte que le Vendeur archive ces informations afin d’assurer un suivi des transactions.<br><br>23. Données à caractère personnel<br><br>Conformément à la loi n°78-17 du 6 janvier 1978 modifiée relative à l’informatique, aux fichiers et aux libertés et au règlement européen 2016/679 du Parlement européen et du Conseil du 27 avril 2016, le Vendeur informe tout utilisateur du Site et tout Client qu’elle respecte la confidentialité, l’intégrité et la sécurité des données que ces derniers peuvent être amenés à lui communiquer par le biais du Site. Le Client dispose, en vertu de ces dispositions, d\'un droit d\'accès, de rectification, de suppression et d’opposition au traitement de données le concernant en adressant une demande au Vendeur à l’adresse de son siège social mentionnée à l’article 1, ou par courrier électronique à l’adresse suivante : « <a href="/#/contact">covilience.org/contact</a> », et en joignant à sa demande une copie de sa pièce d’identité.<br><br>Toute ouverture d’un compte client est soumise à la réalisation d’un traitement de données à caractère personnel sous la responsabilité du Vendeur. Ce traitement est déclaré à la CNIL.<br><br>Le Site a fait l\'objet d\'une déclaration n°1589577v0 auprès de la Commission Nationale Informatique et Liberté (CNIL).<br><br>Le Vendeur collecte et conserve les données exclusivement et pour la durée strictement nécessaire pour l’exécution de ses prestations et en cas de mise en jeu de sa responsabilité contractuelle et/ou délictuelle. Au-delà de cette durée, les données personnelles ne seront plus conservées.<br><br>Le Vendeur pourra utiliser les informations renseignées par le Client à toutes fins, notamment pour cibler les bandeaux publicitaires, améliorer l’utilisation du Site ou contrôler le respect de la loi et des termes des présentes CGV.<br><br>Le Vendeur se réserve le droit de transmettre les données du Client à des partenaires commerciaux. Conformément à l’article 5 (1er paragraphe), a) et à l’article 12 du règlement européen 2016/679 du Parlement européen et du Conseil du 27 avril 2016, le Vendeur s’engage à informer les tiers auxquels les informations ont été communiqués de l’utilisation par le Client du droit d\'accès, de rectification, de suppression et d’opposition au traitement de données le concernant.<br><br>24. Cookies<br><br>Afin de mieux servir le Client, le Vendeur mesure le nombre de pages vues, le nombre de visites, ainsi que l’activité des visiteurs sur le Site et leur fréquence de retour. A cet effet, le Vendeur utilise la technologie des « cookies ». Un cookie ne permet pas d’identifier le Client ou un utilisateur du Site. Les cookies enregistrent des informations relatives à la navigation de tout périphérique informatique (ordinateur, smartphone, etc.) sur le Site.<br><br>Les données collectées par les cookies sont uniquement et strictement destinées à un usage interne et ne sont jamais transmises à des tiers. L\'utilisation de cookies, propres ou tiers, n\'étant pas nécessaire au fonctionnement du Site, nécessite un consentement exprès de la part du Client. Le Client peut manifester son consentement ou son opposition à l\'utilisation de cookies en paramétrant son navigateur de manière appropriée.<br><br>25 – Intégralité des conditions<br><br>Le changement de législation, de réglementation ou une décision de justice rendant une ou plusieurs clauses des présentes CGV nulle et non avenue ne saurait affecter la validité des autres stipulations des présentes CGV.<br><br>26 – Litiges<br><br>La loi française est seule applicable avec exclusion de la Convention de Vienne sur la vente internationale de marchandise du 11 avril 1980. Toute contestation sur la validité, l’interprétation, l’exécution, la résolution ou la résiliation de la commande et/ou des présentes, qui n’aura pu être résolue dans un premier temps à l’amiable dans les 60 jours de l’envoi d’une lettre recommandée avec avis de réception notifiant la réclamation, sera soumise au tribunal de commerce d’Aubenas.<br><br>En aucun cas, la responsabilité contractuelle du Vendeur, telle qu’elle pourrait être engagée en application des présentes CGV, ne saurait excéder le montant des sommes payées ou restant à payer par le Client pour l’achat du ou des produits en cause.<br><br>27. Propriété intellectuelle<br><br>Tous les éléments du Site (notamment contenus, codes, architecture, liens hypertextes, etc.) sont protégés au titre du droit d\'auteur et des marques.<br><br>Leur utilisation sans l’autorisation préalable et écrite du Vendeur ou des autres titulaires de droits est susceptible de constituer une contrefaçon et de donner lieu à des poursuites judiciaires.<br><br>Sauf autorisation préalable et expresse du Vendeur, en se connectant au Site, le Client s’interdit de copier ou télécharger tout ou partie de son contenu. Sont notamment interdits : l’extraction, la réutilisation le stockage, la reproduction, la représentation ou la conservation de parties quantitativement ou qualitativement substantielles du Site, sous quelque forme que ce soit, y compris des présentes CGV.</p></div></section> </main>';

class CgvPage extends Component {
    render() {
        return (
            <>
            <div dangerouslySetInnerHTML={{ __html: html }} />
            <Footer />
            </>
        );
    }
}

export default withRouter(CgvPage);
