import firebase, { analytics, auth, firestore } from "../firebase";

const authentication = {};

authentication.signUp = (emailAddress, password) => {
  return new Promise((resolve, reject) => {
    if (!emailAddress || !password) {
      reject();

      return;
    }

    if (auth.currentUser) {
      reject();

      return;
    }

    auth
      .createUserWithEmailAndPassword(emailAddress, password)
      .then((value) => {
        const user = value.user;

        if (!user) {
          reject();

          return;
        }

        const uid = user.uid;

        if (!uid) {
          reject();

          return;
        }

        authentication
          .verifyEmailAddress()
          .then(value => {
            resolve(value);
            // console.log("Email sent");
          });
      })
      .catch((reason) => {
        reject(reason);
      });
  });
};

