// Page produits
authentication.createOrder = () => {
  return new Promise((resolve, reject) => {

    const currentUser = auth.currentUser;

    if (!currentUser) {
      reject();
      return;
    }

    // Ajouter dans cloud functions que ça ajoute champ shipping et facturation
    firestore
      .collection("users/" + currentUser.uid + "/orders/")
      .add({})
      .then((docRef) => {

        docRef.get().then(function (doc) {
          resolve({
            uid: doc.id,
            ...doc.data()
          });
        });
      })
      .catch((reason) => {
        reject(reason);
      });
  });
};

// Page produits
authentication.getPendingOrder = () => {
  return new Promise((resolve, reject) => {

    const currentUser = auth.currentUser;

    if (!currentUser) {
      reject();
      return;
    }

    firestore
      .collection("users/" + currentUser.uid + "/orders/")
      .where("pending", "==", true)
      .get()
      .then(function (querySnapshot) {

        if (!querySnapshot.empty) {
          // console.log("Document found");

          const orderData = {
            uid: querySnapshot.docs[0].id,
            ...querySnapshot.docs[0].data()
          };
          // console.log(orderData);

          resolve(orderData);
        } else {
          // console.log("Document not found");
          resolve(null);
        }

      })
      .catch(function (error) {
        // console.log("Error getting documents: ", error);
        reject(error);
      });

  });
};

// Page produits
authentication.deleteOrder = (orderId) => {
  return new Promise((resolve, reject) => {

    const currentUser = auth.currentUser;

    if (!currentUser) {
      reject();
      return;
    }

    firestore
      .collection("users/" + currentUser.uid + "/orders/")
      .doc(orderId)
      .delete()
      .then(() => {
        // console.log("Document deleted");
        resolve(true);
      })
      .catch(function (error) {
        // console.log("Error deleting documents: ", error);
        reject(error);
      });
  });
};

// Page produits
authentication.getProducts = (orderId) => {
  return new Promise((resolve, reject) => {

    const currentUser = auth.currentUser;

    if (!currentUser) {
      reject();
      return;
    }

    firestore
      .collection("users/" + currentUser.uid + "/orders/" + orderId + "/products/")
      .where("quantity", ">", 0)
      .get()
      .then(querySnapshot => {

        if (!querySnapshot.empty) {

          let orderProducts = [];

          querySnapshot.forEach(function (doc) {
            orderProducts.push({
              uid: doc.id,
              ...doc.data()
            });
          });

          // console.log("Document found", orderProducts);

          resolve(orderProducts);

        } else {
          // console.log("Documents not found");
          resolve(null);
        }

      })
      .catch(function (error) {
        // console.log("Error getting documents: ", error);
        reject(error);
      });

  });
};

// Page produits / basket modal
authentication.updateProduct = (orderId, productId, productQuantity) => {
  return new Promise((resolve, reject) => {

    const currentUser = auth.currentUser;

    if (!currentUser) {
      reject();
      return;
    }

    // Set productId in firestore with merge
    // (cloud function add product details onCreate with hided products collection firestore)
    firestore
      .collection("users/" + currentUser.uid + "/orders/" + orderId + "/products/")
      .doc(productId)
      .set({
        quantity: productQuantity
      }, { merge: true })
      .then(() => {
        // console.log("Product updated");
        resolve(true);
      })
      .catch(function (error) {
        // console.log("Error updating product: ", error);
        reject(error);
      });

  });
};

