// Page commandes
authentication.getOrders = () => {
  return new Promise((resolve, reject) => {

    const currentUser = auth.currentUser;

    if (!currentUser) {
      reject();
      return;
    }

    firestore
      .collection("users/" + currentUser.uid + "/orders/")
      .where("paid", "==", true)
      .get()
      .then(querySnapshot => {

        if (!querySnapshot.empty) {

          let orders = {};

          querySnapshot.forEach(function (doc) {
            orders[doc.id] = {
              uid: doc.id,
              ...doc.data()
            };
          });

          // console.log("Document found", orders);

          resolve(orders);

        } else {
          // console.log("Documents not found");
          resolve(null);
        }

      })
      .catch(function (error) {
        // console.log("Error getting documents: ", error);
        reject(error);
      });

  });
};

// Basket modal / page commandes
authentication.updateOrder = (orderId, newValue) => {
  return new Promise((resolve, reject) => {

    // console.log("Dans updateOrder in authentication");

    const currentUser = auth.currentUser;

    if (!currentUser) {
      reject();
      return;
    }

    firestore
      .collection("users/" + currentUser.uid + "/orders/")
      .doc(orderId)
      .set(newValue,
        { merge: true }
      )
      .then(() => {
        // console.log("Order updated");
        resolve(true);
      })
      .catch(function (error) {
        // console.log("Error updating order: ", error);
        reject(error);
      });

  });
};

authentication.deleteFees = (orderId) => {
  return new Promise((resolve, reject) => {

    // console.log("Dans deleteFees in authentication");

    const currentUser = auth.currentUser;

    if (!currentUser) {
      reject();
      return;
    }

    firestore
      .collection("users/" + currentUser.uid + "/orders/" + orderId + "/products/")
      .where('nature', '==', "fees")
      .get()
      .then(function (querySnapshot) {
        querySnapshot.forEach(function (doc) {
          doc.ref.delete();
        });
        resolve(true);
      })
      .catch(function (error) {
        // console.log("Error updating order: ", error);
        reject(error);
      });
  });
}



