authentication.getUserInformations = () => {
  return new Promise((resolve, reject) => {

    const currentUser = auth.currentUser;

    if (!currentUser) {
      reject();

      return;
    }

    const uid = currentUser.uid;

    if (!uid) {
      reject();

      return;
    }

    firestore
      .collection("users")
      .doc(uid)
      .get()
      .then(doc => {
        if (doc.exists) {
          // console.log("Document data:", doc.data());
          resolve(doc.data());
        } else {
          // doc.data() will be undefined in this case
          // console.log("No such document!");
          reject(false);
        }
      }).catch(function (error) {
        // console.log("Error getting document:", error);
      });
  });
};

authentication.changeUserInformations = (property, value) => {
  return new Promise((resolve, reject) => {
    if (!value) {
      reject();

      return;
    }

    const currentUser = auth.currentUser;

    if (!currentUser) {
      reject();

      return;
    }

    const uid = currentUser.uid;

    if (!uid) {
      reject();

      return;
    }

    firestore
      .collection("users")
      .doc(uid)
      .update({
        [property]: value,
      })
      .then(() => {
        analytics.logEvent("change_" + property);

        resolve(true);
      })
      .catch((reason) => {
        reject(reason);
      });
  });
};



