import React from "react";

import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import ProductDetail from '../ProductDetail';
import PropTypes from "prop-types";
import { Lightbox } from "react-modal-image";

import {
    Card,
    CardActions,
    CardContent,
    CardMedia,
    Button,
    Typography,
    IconButton
} from "@material-ui/core";

import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';

const useStyles = makeStyles((theme) => ({
    productCard: {
        margin: "15px 15px",
        maxWidth: 345,
        minWidth: 345,
        textAlign: 'center'
    },
    media: {
        cursor: "pointer",
        height: 250,
    },
    quantityField: {
        width: "100px",
        direction: "rtl",
    }
}));

function ProductCard(props) {
    const { product, counter, onChange, uiDisabled } = props;
    const classes = useStyles();

    let vat = {
        "masque": 1.055,
        "gel": 1.055,
        "gant": 1.055,
        "surblouse": 1.055,
        "lunettes": 1.055,
        "visière": 1.055,
        "thermomètre": 1.2
    };

    const [open, setOpen] = React.useState(false);
    const [lightBox, setLightBox] = React.useState(false);

    const openDetails = () => {
        setOpen(true);
    };

    const closeDetails = () => {
        setOpen(false);
    };

    const closeLightbox = () => {
        setLightBox(false);
    };

    const openLightbox = () => {
        setLightBox(true);
    };

    return (
        <Card className={classes.productCard}>
            <CardMedia
                className={classes.media}
                image={product.images[0].url}
                title={product.images[0].name}
                onClick={openLightbox}
            />
            <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                    {product.name}
                </Typography>
                {product.type &&
                    <Typography gutterBottom variant="subtitle1" component="h3">
                        {product.type}
                    </Typography>
                }
                <Typography variant="body1" component="span">
                    {`${product.unitPrice}${product.currency} H.T. / ${product.nature}`}
                </Typography>
            </CardContent>
            <CardContent>
                <form noValidate autoComplete="off" onSubmit={e => { e.preventDefault(); }}>

                    <IconButton aria-label="enlever 1" disabled={uiDisabled} onClick={(event) => onChange(product.sku, parseInt(counter) - 1)}>
                        <RemoveIcon />
                    </IconButton>
                    {parseInt(product.batch) > 1 ?
                        <TextField
                            id="outlined-number"
                            label={`Boîte de ${product.batch}`}
                            size="small"
                            className={classes.quantityField}
                            align="right"
                            onChange={(event) => onChange(product.sku, event.target.value)}
                            value={counter}
                            InputLabelProps={{
                                shrink: true,
                            }}
                            variant="outlined"
                        />
                        :
                        <TextField
                            id="outlined-number"
                            label={`Pièce`}
                            size="small"
                            className={classes.quantityField}
                            onChange={(event) => onChange(product.sku, event.target.value)}
                            value={counter}
                            InputLabelProps={{
                                shrink: true,
                            }}
                            variant="outlined"
                        />
                    }

                    <IconButton aria-label="ajouter 1" disabled={uiDisabled} onClick={(event) => onChange(product.sku, parseInt(counter) + 1)}>
                        <AddIcon />
                    </IconButton>

                </form>

                {parseInt(product.batch) > 1 ?
                    <Typography variant="caption" component="span">
                        {`Soit ${(product.price * vat[product.nature]).toFixed(3).slice(0,-1)}${product.currency} T.T.C la boîte de ${product.batch}`}
                    </Typography>

                    :
                    <Typography variant="caption" component="span">
                        {`Soit ${(product.price * vat[product.nature]).toFixed(3).slice(0,-1)}${product.currency} T.T.C / pièce`}
                    </Typography>
                }

            </CardContent>
            <CardActions style={{ justifyContent: 'center' }}>
                <Button size="small" color="primary" onClick={openDetails}>
                    Fiche produit
                </Button>
            </CardActions>
            <ProductDetail
                details={product}
                open={open}
                handleClose={closeDetails}
            />
            {lightBox &&
                <Lightbox
                    hideZoom={true}
                    medium={product.images[0].url}
                    large={product.images[0].url}
                    onClose={closeLightbox}
                />}
        </Card>
    );
}

ProductCard.propTypes = {
    product: PropTypes.object.isRequired,
};

export default ProductCard;
