import React, { Component } from "react";

import PropTypes from "prop-types";

import { withRouter, Link, Redirect } from "react-router-dom";

import authentication from "../../services/authentication";

import queryString from "query-string";

import {
  Refresh as RefreshIcon,
  Home as HomeIcon,
  ShoppingCart as ShoppingCartIcon,
  Warning as WarningIcon,
  Check as CheckIcon,
  ExpandMore as ExpandMoreIcon,
  NotificationsActive as NotificationsActiveIcon,
  PersonOutline as PersonOutlineIcon,
} from "@material-ui/icons";

import Footer from "../Footer";

import {
  // Tab,
  // Tabs,
  Paper,
  Grid,
  Box,
  Fab,
  Typography,
  // List,
  // ListItem,
  // ListItemAvatar,
  // ListItemIcon,
  // ListItemSecondaryAction,
  // ListItemText,
  // IconButton,
  // FormGroup,
  FormControlLabel,
  Checkbox,
  Divider,
  TextField,
  ExpansionPanel,
  ExpansionPanelDetails,
  ExpansionPanelSummary,
  Button
} from '@material-ui/core';

import Loader from "../Loader";

import { ReactComponent as NoDataIllustration } from "../../illustrations/no-data.svg";

import { withStyles } from "@material-ui/core/styles";

const styles = (theme) => ({
  grid: {
    margin: "50px 0",
    width: "100%",
  },
  textFields: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
    },
  },
  heading: {
    marginLeft: "15px",
    fontSize: theme.typography.pxToRem(15),
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(13),
    color: theme.palette.text.secondary,
    marginLeft: "15px",
  },
  terciaryHeading: {
    fontSize: theme.typography.pxToRem(10),
    color: theme.palette.text.secondary,
    marginLeft: "15px",
  },
  verifyEmail: {
    textAlign: "center",
    padding: "15px",
  },
  divider: {
    margin: "30px 0",
  }
});

class OrderPage extends Component {

  constructor(props) {
    super(props);

    this.state = {
      expanded: false,
      loading: true,
      orders: {},
      products: {},
      ordersArray: []
    }
  }

  componentWillMount() {
    const queryParsed = queryString.parse(this.props.location.search);
    let queryOrderId = queryParsed.orderId;
    let queryState = queryParsed.state;

    if (queryOrderId !== undefined && queryState == "success") {

      authentication
        .updateOrder(queryOrderId, { "paid": true, "pending": false, "cancel": false, "datePaid": Date.now() })
        .then(result => {
          this.getOrders();
        });
    } else {

      this.getOrders();
    }
  }

  getProducts() {

    Object.keys(this.state.orders).map((order) => {
      authentication
        .getProducts(order)
        .then(orderProducts => {

          // console.log("Products of order : ", orderProducts);

          if (orderProducts == null) return;

          this.setState({
            ...this.state,
            products: {
              ...this.state.products,
              [order]: orderProducts
            }
          }, () => {
            // console.log(this.state.products);
          });

        });
    });
  }

  getOrders() {
    authentication
      .getOrders()
      .then((orderDocumentsParam) => {

        let orderDocuments = orderDocumentsParam;

        // console.log("Order documents:", orderDocuments);

        if (orderDocuments == null) {
          this.setState({
            ...this.state,
            loading: false
          });
          return;
        };

        this.updateOrdersIncomplete(orderDocuments);
      });
  }

  sortOrders() {
    const ordersArray = Object.keys(this.state.orders).map((item) => {
      return this.state.orders[item];
    });
    ordersArray.sort(function (a, b) {
      return parseInt(b.datePaid) - parseInt(a.datePaid);
    });

    // console.log("Orders:", ordersArray);

    this.setState({
      ...this.state,
      ordersArray: ordersArray
    })
  }

  updateOrdersIncomplete(orderDocuments) {
    const orderStateUpdated = Object.keys(orderDocuments).map(async (item) => {

      let company = orderDocuments[item].company;
      let facturation = orderDocuments[item].facturation;
      let shipping = orderDocuments[item].shipping;

      let companyCompleted = company.name !== null && company.siret !== null;

      let facturationCompleted = facturation.address1 !== null && facturation.city !== null && facturation.postal_code !== null && facturation.country !== null;

      let shippingCompleted = shipping.address1 !== null && shipping.city !== null && shipping.postal_code !== null && shipping.country !== null;

      shippingCompleted = shipping.sameFacturationAddress ? true : shippingCompleted;

      orderDocuments[item]["incomplete"] = !companyCompleted || !facturationCompleted || !shippingCompleted;

      return true;
    });

    Promise.all(orderStateUpdated).then(() => {
      this.setState({
        ...this.state,
        loading: false,
        orders: orderDocuments
      }, () => {
        setTimeout(() => this.getProducts(), 2000);
        this.sortOrders();
      });
    });
  }

  updateOrderIncomplete(orderId) {

    let company = this.state.orders[orderId].company;
    let facturation = this.state.orders[orderId].facturation;
    let shipping = this.state.orders[orderId].shipping;

    let companyCompleted = company.name !== null && company.siret !== null;

    let facturationCompleted = facturation.address1 !== null && facturation.city !== null && facturation.postal_code !== null && facturation.country !== null;

    let shippingCompleted = shipping.address1 !== null && shipping.city !== null && shipping.postal_code !== null && shipping.country !== null;

    shippingCompleted = shipping.sameFacturationAddress ? true : shippingCompleted;

    let newIncomplete = !companyCompleted || !facturationCompleted || !shippingCompleted;

    this.setState({
      ...this.state,
      orders: {
        ...this.state.orders,
        [orderId]: {
          ...this.state.orders[orderId],
          incomplete: newIncomplete
        }
      }
    });
  }

  handleExpanded = (panel) => (event, isExpanded) => {
    this.setState({
      ...this.state,
      expanded: isExpanded ? panel : false
    });
  }

  handleCheck = (event, orderId, prop, subProp) => {
    this.setState({
      ...this.state,
      orders: {
        ...this.state.orders,
        [orderId]: {
          ...this.state.orders[orderId],
          [prop]: {
            ...this.state.orders[orderId][prop],
            [subProp]: event.target.checked
          }
        }
      }
    })
  }

  handleSubmit = orderId => {
    this.setState({
      ...this.state,
      loading: true,
    });
    authentication
      .updateOrder(orderId, this.state.orders[orderId])
      .then(orderUpdated => {
        // console.log("Order updated");
        this.setState({
          ...this.state,
          loading: false,
        });
        this.updateOrderIncomplete(orderId);
      });
  }

  handleChange = (event, orderId, prop, subProp) => {

    // console.log(event.target.value, orderId, prop, subProp);
    this.setState({
      ...this.state,
      orders: {
        ...this.state.orders,
        [orderId]: {
          ...this.state.orders[orderId],
          [prop]: {
            ...this.state.orders[orderId][prop],
            [subProp]: event.target.value == "" ? null : event.target.value
          }
        }
      }
    })
  }

  render() {
    const { user, classes, onSettingsClick } = this.props;
    const { loading, orders, products, expanded, checked, ordersArray } = this.state;

    let vat = {
      "masque": 1.055,
      "gel": 1.055,
      "gant": 1.055,
      "surblouse": 1.055,
      "lunettes": 1.055,
      "visière": 1.055,
      "thermomètre": 1.2
    };

    if (loading) {
      return <Loader />;
    }

    if (!user) {
      return (
        <Redirect to={{ pathname: "/produits" }} />
      );
    }

    // if le tableau de commande est vide
    if (Object.keys(orders).length < 1) {
      return (
        <Grid className={classes.grid} container justify="center" alignItems="center" spacing={5}>
          <Grid item xs={12} style={{ textAlign: "center" }}>
            <Typography>
              Vous n'avez pas de commandes.
            </Typography>
          </Grid>
          <Grid item xs={12} style={{ textAlign: "center" }}>
            <Button
              variant="contained"
              color="primary"
              size="medium"
              component={Link}
              to="/produits"
            >
              Consulter les produits
            </Button>
          </Grid>
        </Grid>
      );
    }

    return (
      <>
        <Grid className={classes.grid} container justify="center" spacing={5}>

          {!user.emailVerified &&
            <Grid item xs={12} sm={8}>
              <Paper elevation={3} className={classes.verifyEmail}>
                <NotificationsActiveIcon color="secondary" fontSize="large" />
                <Typography variant="body1" component="span">
                  Veuillez activer votre compte en cliquant sur le lien d'activation envoyé à votre email : {user.email}
                </Typography>
              </Paper>
            </Grid>
          }

          <Grid item xs={12} sm={8}>
            <Box>
              <Button
                variant="contained"
                color="secondary"
                size="medium"
                startIcon={<PersonOutlineIcon />}
                onClick={(event) => onSettingsClick()}
              >
                Vos informations
          </Button>
            </Box>
          </Grid>

          <Grid item xs={12} sm={8}>
            <Typography my={4} variant="h6" className={classes.title}>
              Vos commandes
          </Typography>
            <div className={classes.list}>

              {ordersArray.map((item) => {

                let minutes = new Date(parseInt(item.datePaid)).getMinutes();
                minutes = String(minutes).length == 1 ? `0${minutes}` : minutes;

                let hours = new Date(parseInt(item.datePaid)).getHours();
                hours = String(hours).length == 1 ? `0${hours}` : hours;

                let day = new Date(parseInt(item.datePaid)).getDate();
                day = String(day).length == 1 ? `0${day}` : day;

                let month = new Date(parseInt(item.datePaid)).getMonth();
                month = String(month).length == 1 ? `0${month}` : month;

                let year = new Date(parseInt(item.datePaid)).getFullYear();

                let datePaidFormatted = `${day}/${month}/${year} à ${hours}:${minutes}`;

                return <ExpansionPanel key={item.uid} expanded={expanded === item.uid} onChange={this.handleExpanded(item.uid)}>

                  <ExpansionPanelSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1bh-content"
                  >
                    {orders[item.uid].incomplete ?
                      <WarningIcon color="secondary" />
                      : <CheckIcon color="primary" />}
                    <Typography className={classes.heading}>
                      Commande du {datePaidFormatted}
                    </Typography>
                    {orders[item.uid].incomplete ?
                      <Typography className={classes.secondaryHeading}>Complétez vos informations</Typography>
                      : null}
                  </ExpansionPanelSummary>

                  <ExpansionPanelDetails>
                    <form noValidate autoComplete="off" onSubmit={(e) => e.preventDefault()}>

                      {products[item.uid] != undefined &&
                        <Box mb={2} className={classes.textFields}>
                          <Typography my={2}>Panier</Typography>

                          {products[item.uid].map(product => {

                            if (product.quantity == undefined) return;

                            if (product.nature == undefined) return <Typography className={classes.secondaryHeading} variant="caption" my={2}>Produit en attente de validation...</Typography>;

                            if (product.nature == "fees") {

                              // Il s'agit d'un frais de port
                              return (
                                <Box key={product.uid}><Typography className={classes.secondaryHeading} my={2}>
                                  {product.name} : {product.price}{product.currency}
                                </Typography>
                                </Box>
                              )
                            }

                            // Il s'agit d'un produit
                            if (parseInt(product.batch) > 1) {
                              return (
                                <Box key={product.uid}>
                                  <Typography className={classes.secondaryHeading} my={2}>
                                    x{product.quantity} {product.name} {product.type || ""} (boîte de {product.batch}) : {(product.quantity * product.price * vat[product.nature]).toFixed(3).slice(0, -1)}{product.currency} T.T.C.
                                    </Typography>
                                </Box>
                              )
                            } else {
                              return (
                                <Box key={product.uid}>
                                  <Typography className={classes.secondaryHeading} my={2}>
                                    x{product.quantity} {product.name} {product.type || ""} : {(product.quantity * product.price * vat[product.nature]).toFixed(3).slice(0, -1)}{product.currency} T.T.C.
                                    </Typography>
                                </Box>
                              )
                            }


                          })}

                          {orders[item.uid].comments &&
                            <TextField
                              id="comments-textfield"
                              label="Commentaires laissés"
                              style={{ marginTop: "20px" }}
                              multiline
                              fullWidth
                              disabled={true}
                              value={orders[item.uid].comments.beforeCheckout}
                            />
                          }

                        </Box>}

                      <Box my={6} className={classes.textFields}>
                        <Typography my={2}>Informations de l'établissement</Typography>
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "company", "name")}
                          value={orders[item.uid].company.name}
                          label="Raison sociale (obligatoire)"
                        />
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "company", "siret")}
                          value={orders[item.uid].company.siret}
                          label="SIRET (obligatoire)"
                        />
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "company", "vat")}
                          value={orders[item.uid].company.vat}
                          label="TVA"
                        />
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "company", "activity")}
                          value={orders[item.uid].company.activity}
                          label="Activité"
                        />
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "company", "phone")}
                          value={orders[item.uid].company.phone}
                          label="Téléphone"
                        />
                      </Box>

                      <Box my={6} className={classes.textFields}>
                        <Typography my={2}>Facturation</Typography>
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "facturation", "address1")}
                          value={orders[item.uid].facturation.address1}
                          label="Adresse ligne 1 (obligatoire)"
                        />
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "facturation", "address2")}
                          value={orders[item.uid].facturation.address2}
                          label="Adresse ligne 2"
                        />
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "facturation", "address3")}
                          value={orders[item.uid].facturation.address3}
                          label="Adresse ligne 3"
                        />
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "facturation", "city")}
                          value={orders[item.uid].facturation.city}
                          label="Ville (obligatoire)"
                        />
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "facturation", "postal_code")}
                          value={orders[item.uid].facturation.postal_code}
                          label="Code postal (obligatoire)"
                        />
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "facturation", "country")}
                          value={orders[item.uid].facturation.country}
                          label="Pays (obligatoire)"
                        />
                      </Box>

                      <Box my={6} className={classes.textFields}>
                        <Typography my={2}>Livraison</Typography>
                        {!orders[item.uid].shipping.sameFacturationAddress &&
                          <Box>
                            <TextField fullWidth
                              onChange={(event) => this.handleChange(event, item.uid, "shipping", "address1")}
                              value={orders[item.uid].shipping.address1}
                              label="Adresse ligne 1 (obligatoire)"
                            />
                            <TextField fullWidth
                              onChange={(event) => this.handleChange(event, item.uid, "shipping", "address2")}
                              value={orders[item.uid].shipping.address2}
                              label="Adresse ligne 2"
                            />
                            <TextField fullWidth
                              onChange={(event) => this.handleChange(event, item.uid, "shipping", "address3")}
                              value={orders[item.uid].shipping.address3}
                              label="Adresse ligne 3"
                            />
                            <TextField fullWidth
                              onChange={(event) => this.handleChange(event, item.uid, "shipping", "city")}
                              value={orders[item.uid].shipping.city}
                              label="Ville (obligatoire)"
                            />
                            <TextField fullWidth
                              onChange={(event) => this.handleChange(event, item.uid, "shipping", "postal_code")}
                              value={orders[item.uid].shipping.postal_code}
                              label="Code postal (obligatoire)"
                            />
                            <TextField fullWidth
                              onChange={(event) => this.handleChange(event, item.uid, "shipping", "country")}
                              value={orders[item.uid].shipping.country}
                              label="Pays (obligatoire)"
                            />
                          </Box>
                        }
                        <FormControlLabel
                          control={<Checkbox
                            checked={orders[item.uid].shipping.sameFacturationAddress}
                            onChange={(event) => this.handleCheck(event, item.uid, "shipping", "sameFacturationAddress")}
                            inputProps={{ 'aria-label': 'primary checkbox' }}
                          />}
                          label="Même adresse de livraison et de facturation"
                        />
                      </Box>

                      <Box my={6} className={classes.textFields}>
                        <Typography my={2}>Informations supplémentaires</Typography>
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "shippingComplementary", "phone")}
                          value={orders[item.uid].shippingComplementary.phone}
                          label="Téléphone"
                        />
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "shippingComplementary", "floor")}
                          value={orders[item.uid].shippingComplementary.floor}
                          label="Étage"
                        />
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "shippingComplementary", "interphone")}
                          value={orders[item.uid].shippingComplementary.interphone}
                          label="Interphone"
                        />
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "shippingComplementary", "code")}
                          value={orders[item.uid].shippingComplementary.code}
                          label="Code"
                        />
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "shippingComplementary", "service")}
                          value={orders[item.uid].shippingComplementary.service}
                          label="Service"
                        />
                        <TextField fullWidth
                          onChange={(event) => this.handleChange(event, item.uid, "shippingComplementary", "message")}
                          value={orders[item.uid].shippingComplementary.message}
                          label="Message"
                        />
                      </Box>

                      <Button
                        variant="contained"
                        color="primary"
                        size="medium"
                        startIcon={<RefreshIcon />}
                        onClick={(event) => this.handleSubmit(item.uid)}
                      >
                        Mettre à jour
                    </Button>
                    </form>
                  </ExpansionPanelDetails>

                </ExpansionPanel>

              })}

            </div>
          </Grid>
        </Grid >
        <Footer />
      </>
    );
  }
}

OrderPage.propTypes = {
  user: PropTypes.object,
};

export default withRouter(withStyles(styles)(OrderPage));
