import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import {
    Fab,
    Badge,
    Zoom
} from '@material-ui/core';
import PropTypes from "prop-types";
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';

const StyledBadge = withStyles((theme) => ({
    badge: {
        right: -5,
        top: -10,
        padding: '4px',
    },
}))(Badge);

const useStyles = makeStyles((theme) => ({
    fab: {
        zIndex: 10,
        position: 'fixed',
        height: '80px',
        width: '80px',
        bottom: theme.spacing(6),
        right: theme.spacing(6),
        [theme.breakpoints.down('sm')]: {
            height: '75px',
            width: '75px',
            bottom: theme.spacing(3),
            right: theme.spacing(3),
        },
    },
}));

function ProductBasket(props) {
    const { counters, handleOpen } = props;
    const classes = useStyles();

    let countersCopy = JSON.parse(JSON.stringify(counters));
    delete countersCopy["sku_HBxr5SJ5wb0frc"];
    delete countersCopy["sku_HBxskq26k7lMJg"];
    delete countersCopy["sku_HBxti3j6pdJyzc"];

    let itemNumber = Object.values(countersCopy).reduce((a, b) => a + b, 0);
    let showBtn = itemNumber > 0 ? true : false;
    // console.log(itemNumber, counters);

    return (
        <>
            <Zoom in={showBtn}>
                <Fab
                    color="primary"
                    aria-label="ShoppingCart"
                    className={classes.fab}
                    onClick={handleOpen}
                >
                    <StyledBadge badgeContent={itemNumber} color="secondary">
                        <ShoppingCartIcon fontSize="large" />
                    </StyledBadge>
                </Fab>
            </Zoom>
        </>
    );
}

ProductBasket.propTypes = {
    counters: PropTypes.object.isRequired,
    handleOpen: PropTypes.func.isRequired,
};

export default ProductBasket;