import React, { Component } from "react";

import PropTypes from "prop-types";

import { Link } from "react-router-dom";

import {
  AppBar,
  Toolbar,
  Typography,
  Box,
  Button,
  IconButton,
  Divider,
  Menu,
  MenuItem,
  Slide,
  useScrollTrigger,
  SwipeableDrawer,
  List,
  ListItem,
  ListItemText,
} from "@material-ui/core";

import {
  AccountCircle as AccountCircleIcon,
} from "@material-ui/icons";

import MenuIcon from '@material-ui/icons/Menu';

import VpnKeyIcon from '@material-ui/icons/VpnKey';

import { withStyles } from "@material-ui/core/styles";

const styles = (theme) => ({
  root: {
    flexGrow: 1,
  },
  appBar: {
    background: '#f5f5f5',
    fontWeight: 0,
  },
  menuButton: {
    marginRight: theme.spacing(2),
    display: 'block',
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
  logo: {
    maxWidth: 64,
    margin: theme.spacing(2),
  },
  title: {
    fontFamily: "'Open Sans', sans-serif",
    fontWeight: 400,
  },
  subTitle: {
    fontSize: '0.8em',
    fontStyle: 'italic',
    textAlign: 'right',
  },
  menuLinks: {
    display: 'none',
    [theme.breakpoints.up('md')]: {
      display: 'block',
    },
  },
  list: {
    width: 250,
  },
});

class Bar extends Component {
  constructor(props) {
    super(props);

    this.state = {
      menu: {
        anchorEl: null,
      },
      drawer: {
        left: false,
      },
    };
  }

  toggleDrawer = (open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    this.setState({
      drawer: {
        left: open,
      },
    });
  }

  openMenu = (event) => {
    const anchorEl = event.currentTarget;

    this.setState({
      menu: {
        anchorEl,
      },
    });
  };

  closeMenu = () => {
    this.setState({
      menu: {
        anchorEl: null,
      },
    });
  };

  render() {
    // Styling
    const { classes } = this.props;

    // Properties
    const { performingAction, user, userData } = this.props;

    // Events
    const {
      onSettingsClick,
      onSignOutClick,
      onConnectClick
    } = this.props;

    const { menu } = this.state;

    const menuItems = [
      {
        name: "Mes commandes",
        to: user ? `/commandes` : null,
      },
      {
        name: "Mon profil",
        onClick: onSettingsClick,
      },
      {
        name: "Déconnexion",
        divide: true,
        onClick: onSignOutClick,
      },
    ];

    return (
      <div className={classes.root}>
        <HideOnScroll>
          <AppBar className={classes.appBar} color="inherit">
            <Toolbar>
              <IconButton
                className={classes.menuButton}
                edge="start"
                color="inherit"
                aria-label="open drawer"
                onClick={this.toggleDrawer(true)}
              >
                <MenuIcon />
              </IconButton>

              <img src="logo192.png" alt="logo" className={classes.logo} />

              <Box>
                <Typography className={classes.title} color="inherit" variant="h5">
                  {process.env.REACT_APP_TITLE}
                </Typography>
                <Typography className={classes.subTitle} color="inherit" variant="subtitle1">
                  by Option Public
                </Typography>
              </Box>

              <Box flexGrow={1}></Box>

              <Box className={classes.menuLinks}>
                <Button color="inherit"
                  component={Link} to="/"
                  target="_self"
                >Accueil</Button>
                <Button color="inherit"
                  component={Link} to="/produits"
                >Produits</Button>
                <Button color="inherit"
                  component={Link} to="/guide-pratique"
                >Guide pratique</Button>
                <Button color="inherit"
                  component={Link} to="/soutiens"
                >Soutiens</Button>
                <Button color="inherit"
                  component={Link} to="/ressources"
                >Ressources</Button>
                <Button color="inherit"
                  component={Link} to="/contact"
                >Contact</Button>
              </Box>

              {user && (
                <>

                  <IconButton
                    color="inherit"
                    disabled={performingAction}
                    onClick={this.openMenu}
                  >
                    <AccountCircleIcon />
                  </IconButton>

                  <Menu
                    anchorEl={menu.anchorEl}
                    open={Boolean(menu.anchorEl)}
                    onClose={this.closeMenu}
                  >
                    {menuItems.map((menuItem, index) => {
                      if (
                        menuItem.hasOwnProperty("condition") &&
                        !menuItem.condition
                      ) {
                        return null;
                      }

                      let component = null;

                      if (menuItem.to) {
                        component = (
                          <MenuItem
                            key={index}
                            component={Link}
                            to={menuItem.to}
                            onClick={this.closeMenu}
                          >
                            {menuItem.name}
                          </MenuItem>
                        );
                      } else {
                        component = (
                          <MenuItem
                            key={index}
                            onClick={() => {
                              this.closeMenu();

                              menuItem.onClick();
                            }}
                          >
                            {menuItem.name}
                          </MenuItem>
                        );
                      }

                      if (menuItem.divide) {
                        return (
                          <span key={index}>
                            <Divider />

                            {component}
                          </span>
                        );
                      }

                      return component;
                    })}
                  </Menu>
                </>
              )}

              {!user && (
                <Button
                  color="inherit"
                  disabled={performingAction}
                  onClick={onConnectClick}>
                  <VpnKeyIcon />
                </Button>
              )}

            </Toolbar>
          </AppBar>
        </HideOnScroll>
        <SwipeableDrawer
          anchor={"left"}
          open={this.state.drawer.left}
          onClose={this.toggleDrawer(false)}
          onOpen={this.toggleDrawer(true)}
        >
          <div
            className={classes.list}
            role="presentation"
            onClick={this.toggleDrawer(false)}
            onKeyDown={this.toggleDrawer(false)}
          >
            <List>
              {user && (
                <ListItem>

                  <IconButton
                    color="inherit"
                    disabled={performingAction}
                    onClick={this.openMenu}
                  >
                    <AccountCircleIcon />
                  </IconButton>

                  <Menu
                    anchorEl={menu.anchorEl}
                    open={Boolean(menu.anchorEl)}
                    onClose={this.closeMenu}
                  >
                    {menuItems.map((menuItem, index) => {
                      if (
                        menuItem.hasOwnProperty("condition") &&
                        !menuItem.condition
                      ) {
                        return null;
                      }

                      let component = null;

                      if (menuItem.to) {
                        component = (
                          <MenuItem
                            key={index}
                            component={Link}
                            to={menuItem.to}
                            onClick={this.closeMenu}
                          >
                            {menuItem.name}
                          </MenuItem>
                        );
                      } else {
                        component = (
                          <MenuItem
                            key={index}
                            onClick={() => {
                              this.closeMenu();

                              menuItem.onClick();
                            }}
                          >
                            {menuItem.name}
                          </MenuItem>
                        );
                      }

                      if (menuItem.divide) {
                        return (
                          <span key={index}>
                            <Divider />

                            {component}
                          </span>
                        );
                      }

                      return component;
                    })}
                  </Menu>
                </ListItem>
              )}

              {!user &&
                <ListItem
                >
                  <Button
                    color="inherit"
                    disabled={performingAction}
                    onClick={onConnectClick}>
                    <VpnKeyIcon />
                  </Button>
                </ListItem>
              }
              <ListItem button
                component={Link} to="/"
              >
                <ListItemText primary={"Accueil"} />
              </ListItem>
              <ListItem button
                component={Link} to="/produits"
              >
                <ListItemText primary={"Produits"} />
              </ListItem>
              <ListItem button
                component={Link} to="/guide-pratique"
              >
                <ListItemText primary={"Guide pratique"} />
              </ListItem>
              <ListItem button
                component={Link} to="/soutiens"
              >
                <ListItemText primary={"Soutiens"} />
              </ListItem>
              <ListItem button
                component={Link} to="/ressources"
              >
                <ListItemText primary={"Ressources"} />
              </ListItem>
              <ListItem button
                component={Link} to="/contact"
              >
                <ListItemText primary={"Contact"} />
              </ListItem>

            </List>
            <Divider />
            <List>
              <ListItem button
                component={Link} to="/mentions-legales"
              >
                <ListItemText primary={"Mentions Légales & Conditions d'utilisation"} />
              </ListItem>
              <ListItem button
                component={Link} to="/conditions-vente"
              >
                <ListItemText primary={"Conditions générales de vente"} />
              </ListItem>
            </List>
          </div>
        </SwipeableDrawer>
      </div >
    );
  }
}

function HideOnScroll(props) {
  const { children } = props;
  const trigger = useScrollTrigger();

  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}

HideOnScroll.propTypes = {
  children: PropTypes.element.isRequired,
};

Bar.defaultProps = {
  performingAction: false,
};

Bar.propTypes = {
  // Properties
  performingAction: PropTypes.bool.isRequired,
  user: PropTypes.object,
  userData: PropTypes.object,

  // Events
  onConnectClick: PropTypes.func.isRequired,
  onSettingsClick: PropTypes.func.isRequired,
  onSignOutClick: PropTypes.func.isRequired,
};


export default withStyles(styles)(Bar);
