import React from "react";
import { makeStyles } from '@material-ui/core/styles';
import PropTypes from "prop-types";
import {
    List,
    ListItem,
    ListItemText,
    Dialog,
    // DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Slide,
    IconButton,

} from '@material-ui/core';
// import SaveAltIcon from '@material-ui/icons/SaveAlt';
import CloseIcon from '@material-ui/icons/Close';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const useStyles = makeStyles((theme) => ({
    listItemText: {
        fontWeight: "bold",
    },
    closeButton: {
        position: "absolute",
        right: theme.spacing(1),
        top: theme.spacing(1),
    },
}));

function ProductDetail(props) {
    const { details, open, handleClose } = props;
    const classes = useStyles();

    return (
        <div>
            <Dialog
                open={open}
                TransitionComponent={Transition}
                keepMounted
                onClose={handleClose}
                aria-labelledby="alert-dialog-slide-title"
                aria-describedby="alert-dialog-slide-description"
            >
                <IconButton
                    className={classes.closeButton}
                    onClick={handleClose}
                >
                    <CloseIcon />
                </IconButton>
                <DialogTitle id="alert-dialog-slide-title" style={{ textAlign: 'center' }}>{"Fiche produit"}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-slide-description">
                        <List>
                            <ListItem dense={true}>
                                <ListItemText
                                    classes={{ primary: classes.listItemText }}
                                    primary={`Nom produit`}
                                    secondary={details.name}
                                />
                            </ListItem>

                            {details.type &&
                                <ListItem dense={true}>
                                    <ListItemText
                                        classes={{ primary: classes.listItemText }}
                                        primary={`Type`}
                                        secondary={details.type}
                                    />
                                </ListItem>
                            }

                            {details.materials &&
                                <ListItem dense={true}>
                                    <ListItemText
                                        classes={{ primary: classes.listItemText }}
                                        primary={`Fabrication`}
                                        secondary={details.materials}
                                    />
                                </ListItem>
                            }
                            {details.filtration &&
                                <ListItem dense={true}>
                                    <ListItemText
                                        classes={{ primary: classes.listItemText }}
                                        primary={`Filtration`}
                                        secondary={details.filtration}
                                    />
                                </ListItem>
                            }
                            {details.filter &&
                                <ListItem dense={true}>
                                    <ListItemText
                                        classes={{ primary: classes.listItemText }}
                                        primary={`Filtres`}
                                        secondary={details.filter}
                                    />
                                </ListItem>
                            }
                            {details.iso &&
                                <ListItem dense={true}>
                                    <ListItemText
                                        classes={{ primary: classes.listItemText }}
                                        primary={`ISO`}
                                        secondary={details.iso}
                                    />
                                </ListItem>
                            }

                            {details.ingredients &&
                                <ListItem dense={true}>
                                    <ListItemText
                                        classes={{ primary: classes.listItemText }}
                                        primary={`Ingredients`}
                                        secondary={details.ingredients.join(", ")}
                                    />
                                </ListItem>
                            }

                            {details.volume &&
                                <ListItem dense={true}>
                                    <ListItemText
                                        classes={{ primary: classes.listItemText }}
                                        primary="Taille"
                                        secondary={details.volume}
                                    />
                                </ListItem>
                            }

                            {details.viscosity &&
                                <ListItem dense={true}>
                                    <ListItemText
                                        classes={{ primary: classes.listItemText }}
                                        primary="Viscosité"
                                        secondary={details.viscosity}
                                    />
                                </ListItem>
                            }

                            {details.alcohol &&
                                <ListItem dense={true}>
                                    <ListItemText
                                        classes={{ primary: classes.listItemText }}
                                        primary="Alcool"
                                        secondary={details.alcohol}
                                    />
                                </ListItem>
                            }

                            {details.ph &&
                                <ListItem dense={true}>
                                    <ListItemText
                                        classes={{ primary: classes.listItemText }}
                                        primary="PH"
                                        secondary={details.ph}
                                    />
                                </ListItem>
                            }

                            {details.standards &&
                                <ListItem dense={true}>
                                    <ListItemText
                                        classes={{ primary: classes.listItemText }}
                                        primary={`${details.standards.chinese.name}`}
                                        secondary={details.standards.chinese.value}
                                    />
                                </ListItem>
                            }

                            {details.standards &&
                                <ListItem dense={true}>
                                    <ListItemText
                                        classes={{ primary: classes.listItemText }}
                                        primary={`${details.standards.european.name}`}
                                        secondary={details.standards.european.value}
                                    />
                                </ListItem>
                            }
                        </List>
                    </DialogContentText>
                </DialogContent>
                {/* <DialogActions>
                    <IconButton onClick={handleClose} color="primary" aria-label="save icon">
                        <SaveAltIcon />
                    </IconButton>
                </DialogActions> */}
            </Dialog>
        </div>
    );
}

ProductDetail.propTypes = {
    details: PropTypes.object.isRequired,
    handleClose: PropTypes.func.isRequired,
    open: PropTypes.object.isRequired
};

export default ProductDetail;
