import React, { Component } from "react";

import { withRouter } from "react-router-dom";
import Footer from "../Footer";

const html = '<main role="main"> <section class="container-fluid" style="position: relative; height: 350px; background-image: linear-gradient(var(--blue), var(--blue-light));"> <div class="row justify-content-center" style="height: 100%"> <h2 class="col-12 text-center align-self-center align-middle text-white">Contactez-nous</h2> <svg style="position: absolute; left:0; bottom: 0; width: 100%; height: 50px;" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1416.99 174.01" preserveAspectRatio="none"> <path style="" fill="#fafafa" d="M0,280.8S283.66,59,608.94,163.56s437.93,150.57,808,10.34V309.54H0V280.8Z" transform="translate(0 -135.53)"/></svg> </div></section> <section style="text-align:center;"> <iframe id="googleForm" src="https://docs.google.com/forms/d/e/1FAIpQLSezz1K3V8KzZRg6Jr0U-WAi5nieNheRRx8CcWtal4oV72QlCA/viewform?embedded=true" width="100%" height="1500" frameborder="0" marginheight="0" marginwidth="0"></iframe> </section> </main>';

class ContactPage extends Component {
    render() {
        return (
            <>
            <div dangerouslySetInnerHTML={{ __html: html }} />
            <Footer />
            </>
        );
    }
}

export default withRouter(ContactPage);
