import React, { Component } from "react";

import PropTypes from "prop-types";

import validate from "validate.js";

import { withStyles } from "@material-ui/core/styles";

import {
    Dialog,
    DialogTitle,
    DialogContent,
    Typography,
    Tooltip,
    IconButton,
    Grid,
    Button,
    TextField,
    ExpansionPanel,
    ExpansionPanelSummary,
    ExpansionPanelDetails,
    List,
    ListItem,
    ListItemText,
} from "@material-ui/core";

import {
    Close as CloseIcon,
    ArrowBack as ArrowBackIcon,
    ExpandMore as ExpandMoreIcon,
} from "@material-ui/icons";

import constraints from "../../constraints";
import authentication from "../../services/authentication";

import Loader from '../Loader';

const styles = (theme) => ({
    closeButton: {
        position: "absolute",
        right: theme.spacing(1),
        top: theme.spacing(1),
    },
    backButton: {
        position: "absolute",
        left: theme.spacing(1),
        top: theme.spacing(1),
    },
    icon: {
        marginRight: theme.spacing(0.5),
    },
    divider: {
        margin: "auto",
    },
    grid: {
        marginBottom: theme.spacing(2),
    },
    connectionBtn: {
        margin: "10px",
    },
    emailFields: {
        margin: "10px",
    },
    problemGrid: {
        marginTop: theme.spacing(4),
    },
    problemPanel: {
        [theme.breakpoints.up('sm')]: {
            width: "50%",
        },
        width: "80%",
    },
});

const initialState = {
    performingAction: false,
    emailAddress: "",
    password: "",
    errors: null,
    loader: false,
    connectionType: null,
};

class ConnectDialog extends Component {
    constructor(props) {
        super(props);

        this.state = initialState;
    }

    changeConnectionType = (newConnectionType) => {
        this.setState({
            ...this.state,
            connectionType: newConnectionType,
        })
    };

    signIn = () => {
        const { emailAddress, password } = this.state;

        const errors = validate(
            {
                emailAddress: emailAddress,
                password: password,
            },
            {
                emailAddress: constraints.emailAddress,
                password: constraints.password,
            }
        );

        if (errors) {
            this.setState({
                errors: errors,
            });
        } else {
            this.setState(
                {
                    performingAction: true,
                    errors: null,
                },
                () => {
                    authentication
                        .signIn(emailAddress, password)
                        .then((user) => {
                            this.props.dialogProps.onClose(() => {
                                // const displayName = user.displayName;
                                // const emailAddress = user.email;

                                this.props.openSnackbar(
                                    "Vous êtes connecté!"
                                );
                            });
                        })
                        .catch((reason) => {
                            const code = reason.code;
                            const message = reason.message;

                            this.setState({
                                ...this.state,
                                loader: false,
                            });

                            switch (code) {
                                case "auth/invalid-email":
                                case "auth/user-disabled":
                                case "auth/user-not-found":
                                case "auth/wrong-password":
                                    this.props.openSnackbar(message);
                                    return;

                                default:
                                    this.props.openSnackbar(message);
                                    return;
                            }
                        })
                        .finally(() => {
                            this.setState({
                                performingAction: false,
                            });
                        });
                }
            );
        }
    };

    signUp = () => {
        const { emailAddress, password } = this.state;

        const errors = validate(
            {
                emailAddress: emailAddress,
                password: password,
            },
            {
                emailAddress: constraints.emailAddress,
                password: constraints.password,
            }
        );

        if (errors) {
            this.setState({
                errors: errors,
            });
        } else {
            this.setState(
                {
                    performingAction: true,
                    errors: null,
                    loader: true
                },
                () => {
                    authentication
                        .signUpWithEmailAddressAndPassword(emailAddress, password)
                        .then((user) => {


                            setTimeout(() => window.location.reload(), 2500);

                            this.props.dialogProps.onClose(() => {
                                // const displayName = user.displayName;
                                // const emailAddress = user.email;

                                this.props.openSnackbar(
                                    "Inscription réussie! Rechargement de la page..."
                                );
                            });
                        })
                        .catch((reason) => {
                            const code = reason.code;
                            const message = reason.message;

                            this.setState({
                                ...this.state,
                                loader: false,
                            });

                            switch (code) {
                                case "auth/invalid-email":
                                case "auth/user-disabled":
                                case "auth/user-not-found":
                                case "auth/wrong-password":
                                    this.props.openSnackbar(message);
                                    return;

                                default:
                                    this.props.openSnackbar(message);
                                    return;
                            }
                        })
                        .finally(() => {
                            this.setState({
                                performingAction: false,
                            });
                        });
                }
            );
        }
    };

    // a voir si je garde
    handleKeyPress = (event) => {
        const { emailAddress, password } = this.state;

        if (!emailAddress && !password) {
            return;
        }

        const key = event.key;

        if (event.altKey || event.ctrlKey || event.metaKey || event.shiftKey) {
            return;
        }

        if (key === "Enter") {
            this.signIn();
        }
    };

    handleExited = () => {
        this.setState(initialState);
    };

    handleEmailAddressChange = (event) => {
        const emailAddress = event.target.value;

        this.setState({
            emailAddress: emailAddress,
        });
    };

    handlePasswordChange = (event) => {
        const password = event.target.value;

        this.setState({
            password: password,
        });
    };

    render() {
        // Styling
        const { classes } = this.props;

        // Dialog Properties
        const { dialogProps } = this.props;

        const { loader, performingAction, emailAddress, password, errors, connectionType } = this.state;

        return (
            <Dialog
                fullWidth
                maxWidth="sm"
                disableBackdropClick={performingAction}
                disableEscapeKeyDown={performingAction}
                {...dialogProps}
                // onKeyPress={this.handleKeyPress}
                onExited={this.handleExited}
            >
                {loader ? <Loader /> : null}
                <DialogTitle disableTypography>
                    {connectionType == null &&
                        <Typography variant="h6" style={{ textAlign: "center" }}>Inscrivez/connectez-vous pour continuer</Typography>
                    }
                    {connectionType == "connection" &&
                        <Typography variant="h6" style={{ textAlign: "center" }}>Se connecter</Typography>
                    }
                    {connectionType == "inscription" &&
                        <Typography variant="h6" style={{ textAlign: "center" }}>S'inscrire</Typography>
                    }
                    {connectionType != null &&
                        <Tooltip title="Retour">
                            <IconButton
                                className={classes.backButton}
                                disabled={performingAction}
                                onClick={() => this.setState({ ...this.state, connectionType: null })}
                            >
                                <ArrowBackIcon />
                            </IconButton>
                        </Tooltip>
                    }
                    <Tooltip title="Fermer">
                        <IconButton
                            className={classes.closeButton}
                            disabled={performingAction}
                            onClick={dialogProps.onClose}
                        >
                            <CloseIcon />
                        </IconButton>
                    </Tooltip>
                </DialogTitle>

                <DialogContent>
                    <Grid container direction="row">
                        {connectionType == null &&
                            <Grid item xs align="center">
                                <Button
                                    className={classes.connectionBtn}
                                    variant="contained"
                                    color="primary"
                                    onClick={(e) => this.changeConnectionType("inscription")}
                                >
                                    Inscription
                                    </Button>
                                <Button
                                    className={classes.connectionBtn}
                                    variant="contained"
                                    color="secondary"
                                    onClick={(e) => this.changeConnectionType("connection")}
                                >
                                    Connexion
                                    </Button>
                            </Grid>
                        }

                        {connectionType != null &&
                            <Grid item xs align="center">
                                <TextField
                                    className={classes.emailFields}
                                    disabled={performingAction}
                                    error={!!(errors && errors.emailAddress)}
                                    helperText={
                                        errors && errors.emailAddress
                                            ? errors.emailAddress[0]
                                            : ""
                                    }
                                    label="Adresse e-mail"
                                    required
                                    type="email"
                                    value={emailAddress}
                                    variant="outlined"
                                    InputLabelProps={{ required: false }}
                                    onChange={this.handleEmailAddressChange}
                                />
                                <TextField
                                    className={classes.emailFields}
                                    disabled={performingAction}
                                    error={!!(errors && errors.password)}
                                    helperText={
                                        errors && errors.password ? errors.password[0] : ""
                                    }
                                    label="Mot de passe"
                                    required
                                    type="password"
                                    value={password}
                                    variant="outlined"
                                    InputLabelProps={{ required: false }}
                                    onChange={this.handlePasswordChange}
                                />
                            </Grid>
                        }
                    </Grid>
                    <Grid container direction="row">
                        <Grid item xs align="center">
                            {connectionType == "connection" &&
                                <Button color="primary" disabled={!emailAddress || !password} variant="contained"
                                    onClick={() => this.signIn()}
                                >
                                    Se connecter
                                </Button>
                            }
                            {connectionType == "inscription" &&
                                <Button color="primary" disabled={!emailAddress || !password} variant="contained"
                                    onClick={() => this.signUp()}
                                >
                                    S'inscrire
                                </Button>
                            }
                        </Grid>
                    </Grid>
                    {connectionType != null &&
                        <Grid container className={classes.problemGrid}>
                            <Grid item xs align="center">
                                <ExpansionPanel className={classes.problemPanel}>
                                    <ExpansionPanelSummary
                                        expandIcon={<ExpandMoreIcon />}
                                        aria-controls="panel1a-content"
                                        id="panel1a-header"
                                        style={{ textAlign: "center" }}
                                    >
                                        <Typography>Problème {connectionType == "inscription" ? "d'inscription" : "de connexion"} ?</Typography>
                                    </ExpansionPanelSummary>
                                    <ExpansionPanelDetails>
                                        <List component="nav">
                                            <ListItem>
                                                <ListItemText primary="1. Essayez de désactiver votre bloqueur de pub et de recharger la page" />
                                            </ListItem>
                                            <ListItem>
                                                <ListItemText primary="2. Essayez sur un autre navigateur, ou un autre appareil" />
                                            </ListItem>
                                            <ListItem>
                                                <span>3. <a href="https://forms.gle/mm2Eknt9T4zdRVAr5">Contactez-nous</a></span>
                                            </ListItem>
                                        </List>
                                    </ExpansionPanelDetails>
                                </ExpansionPanel>
                            </Grid>
                        </Grid>
                    }
                </DialogContent>
            </Dialog>
        );
    }
}

ConnectDialog.propTypes = {
    // Styling
    classes: PropTypes.object.isRequired,

    // Dialog Properties
    dialogProps: PropTypes.object.isRequired,

    // Custom Functions
    openSnackbar: PropTypes.func.isRequired,
};

export default withStyles(styles)(ConnectDialog);
