import React from "react";
import { makeStyles } from '@material-ui/core/styles';
import PropTypes from "prop-types";
import {
    Button,
    List,
    ListItem,
    ListItemText,
    Dialog,
    Slide,
    Divider,
    IconButton,
    Typography,
    AppBar,
    Toolbar,
    TextField,
    InputLabel,
    MenuItem,
    Select,
    Avatar
} from '@material-ui/core';

import CloseIcon from '@material-ui/icons/Close';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import LocalShippingIcon from '@material-ui/icons/LocalShipping';

const useStyles = makeStyles((theme) => ({
    appBar: {
        position: 'relative',
    },
    title: {
        marginLeft: theme.spacing(2),
        flex: 1,
    },
    quantityField: {
        width: "120px",
        direction: "rtl",
    },
    avatar: {
        margin: theme.spacing(2),
    },
    avatarShipping: {
        margin: theme.spacing(2),
    },
    formFieldsUpSm: {
        [theme.breakpoints.only('xs')]: {
            display: "none",
        },
    },
    formFieldsDownSm: {
        [theme.breakpoints.up('sm')]: {
            display: "none",
        },
        justifyContent: "center",
    },
    commentField: {
        [theme.breakpoints.up('sm')]: {
            width: "70%",
        },
        [theme.breakpoints.only('xs')]: {
            width: "90%",
        },
    }
}));

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

function ProductDetail(props) {
    const { products, counters, openCart, handleClose, onChange, shipping, handleShipping, comment, handleComment, handleCheckout, uiDisabled } = props;
    const classes = useStyles();

    let productsFormatted = products;
    let isFreeShipping;
    
    let vat = {
        "masque": 1.055,
        "gel": 1.055,
        "gant": 1.055,
        "surblouse": 1.055,
        "lunettes": 1.055,
        "visière": 1.055,
        "thermomètre": 1.2
    };

    const calcTTC = (price, quantity, nature) => {
        
        let htPrice = (price * quantity).toFixed(3).slice(0, -1);
        let ttcPrice = (Number(htPrice) * vat[nature]).toFixed(3).slice(0, -1);

        return ttcPrice;
    }

    products.map((item, index) => {
        if (counters[item.sku] == undefined) return;

        productsFormatted[index].quantity = counters[item.sku];
        productsFormatted[index].finalPrice = ((Number(productsFormatted[index].price) * Number(productsFormatted[index].quantity)) * vat[productsFormatted[index].nature]).toFixed(3).slice(0, -1);
        return;
    });

    let cart = productsFormatted.reduce((a, b) => ({ finalPrice: Number(a.finalPrice) + Number(b.finalPrice) }));

    isFreeShipping = Number(JSON.parse(JSON.stringify(cart.finalPrice)));

    cart.finalPrice = (Number(cart.finalPrice) + Number(shipping.price)).toFixed(3).slice(0, -1);

    if (isFreeShipping >= 500 && shipping.country != "Free") {
        handleShipping("Free");
    } else if (isFreeShipping < 500 && shipping.country == "Free") {
        handleShipping("France");
    }

    return (
        <div>
            <Dialog fullScreen open={openCart} onClose={handleClose} TransitionComponent={Transition}>
                <AppBar className={classes.appBar}>
                    <Toolbar>
                        <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
                            <CloseIcon />
                        </IconButton>
                        <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="cart">
                            <ShoppingCartIcon />
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>
                            Vue d'ensemble du panier
                        </Typography>
                    </Toolbar>
                </AppBar>
                <List>
                    {productsFormatted.map((item, index) => {
                        return item.quantity > 0 ?
                            <React.Fragment key={index}>
                                <ListItem className={classes.itemDetails}>
                                    <Avatar className={classes.avatar} alt={item.images[0].name} src={item.images[0].url} />
                                    {item.type ?
                                        <ListItemText
                                            primary={`${item.name} (${item.type})`}
                                            secondary={`${item.unitPrice}${item.currency} H.T. / ${item.nature}`}
                                        />
                                        :
                                        <ListItemText
                                            primary={`${item.name}`}
                                            secondary={`${item.unitPrice}${item.currency} H.T. / ${item.nature}`}
                                        />
                                    }
                                    <form className={classes.formFieldsUpSm} noValidate autoComplete="off" onSubmit={e => { e.preventDefault(); }}>
                                        <IconButton aria-label="enlever 1" disabled={uiDisabled} onClick={(event) => onChange(item.sku, parseInt(counters[item.sku]) - 1)}>
                                            <RemoveIcon />
                                        </IconButton>
                                        {parseInt(item.batch) > 1 ?
                                            <TextField
                                                id="formFieldsUpSmField"
                                                label={`Boîte de ${item.batch}`}
                                                size="small"
                                                className={classes.quantityField}
                                                onChange={(event) => onChange(item.sku, event.target.value)}
                                                value={counters[item.sku]}
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                                variant="outlined"
                                                helperText={`${calcTTC(item.price, item.quantity, item.nature)}${item.currency} T.T.C.`}
                                            />

                                            :
                                            <TextField
                                                id="formFieldsUpSmField"
                                                label={`Pièce`}
                                                size="small"
                                                className={classes.quantityField}
                                                onChange={(event) => onChange(item.sku, event.target.value)}
                                                value={counters[item.sku]}
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                                variant="outlined"
                                                helperText={`${calcTTC(item.price, item.quantity, item.nature)}${item.currency} T.T.C.`}
                                            />

                                        }
                                        <IconButton aria-label="ajouter 1" disabled={uiDisabled} onClick={(event) => onChange(item.sku, parseInt(counters[item.sku]) + 1)}>
                                            <AddIcon />
                                        </IconButton>
                                    </form>
                                </ListItem>
                                <ListItem className={classes.formFieldsDownSm}>
                                    <form noValidate autoComplete="off" onSubmit={e => { e.preventDefault(); }}>
                                        <IconButton aria-label="enlever 1" disabled={uiDisabled} onClick={(event) => onChange(item.sku, parseInt(counters[item.sku]) - 1)}>
                                            <RemoveIcon />
                                        </IconButton>
                                        {parseInt(item.batch) > 1 ?
                                            <TextField
                                                id="formFieldsDownSmField"
                                                label={`Boîte de ${item.batch}`}
                                                size="small"
                                                className={classes.quantityField}
                                                onChange={(event) => onChange(item.sku, event.target.value)}
                                                value={counters[item.sku]}
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                                variant="outlined"
                                                helperText={`${calcTTC(item.price, item.quantity, item.nature)}${item.currency} T.T.C.`}
                                            />
                                            :
                                            <TextField
                                                id="formFieldsDownSmField"
                                                label={`Pièce`}
                                                size="small"
                                                className={classes.quantityField}
                                                onChange={(event) => onChange(item.sku, event.target.value)}
                                                value={counters[item.sku]}
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                                variant="outlined"
                                                helperText={`${calcTTC(item.price, item.quantity, item.nature)}${item.currency} T.T.C.`}
                                            />
                                        }

                                        <IconButton aria-label="ajouter 1" disabled={uiDisabled} onClick={(event) => onChange(item.sku, parseInt(counters[item.sku]) + 1)}>
                                            <AddIcon />
                                        </IconButton>
                                    </form>
                                </ListItem>
                                <Divider />
                            </React.Fragment>
                            : null
                    })}
                    <ListItem className={classes.itemDetails}>
                        <Avatar className={classes.avatarShipping} color="secondary">
                            <LocalShippingIcon />
                        </Avatar>
                        <ListItemText
                            primary={`Frais de livraison forfaitaire : ${shipping.price}€`}
                            secondary="Offerts pour les commandes supérieures ou égales à 500.00€ T.T.C. En raison de délais d'approvisionnement, veuillez prévoir un délai de livraison d'une quinzaine de jours. Nous travaillons à flux tendu pour vous offrir les tarifs les plus bas."
                        />
                        {isFreeShipping < 500 &&
                            <form className={classes.formFieldsUpSm} noValidate autoComplete="off" onSubmit={e => { e.preventDefault(); }}>
                                <InputLabel id="SmUpShipping">Pays de livraison</InputLabel>
                                <Select
                                    labelId="SmUpShipping"
                                    id="SmUpShippingField"
                                    value={shipping.country}
                                    onChange={(event) => handleShipping(event.target.value)}
                                >
                                    <MenuItem value={"France"}>France</MenuItem>
                                    <MenuItem value={"Europe"}>Europe</MenuItem>
                                    <MenuItem value={"Monde"}>Monde</MenuItem>
                                </Select>
                            </form>
                        }
                    </ListItem>
                    <ListItem className={classes.formFieldsDownSm}>
                        {isFreeShipping < 500 &&
                            <form noValidate autoComplete="off" onSubmit={e => { e.preventDefault(); }}>
                                <InputLabel id="SmDownShipping">Pays de livraison</InputLabel>
                                <Select
                                    labelId="SmDownShipping"
                                    id="SmDownShippingField"
                                    value={shipping.country}
                                    onChange={(event) => handleShipping(event.target.value)}
                                >
                                    <MenuItem value={"France"}>France</MenuItem>
                                    <MenuItem value={"Europe"}>Europe</MenuItem>
                                    <MenuItem value={"Monde"}>Monde</MenuItem>
                                </Select>
                            </form>
                        }
                    </ListItem>
                    <Divider />
                    <ListItem style={{ justifyContent: "center", marginTop: "40px" }}>
                        <Button size="large" variant="contained" color="primary" onClick={handleCheckout}>
                            Commander
                        </Button>
                    </ListItem>
                    <ListItem style={{ textAlign: "center" }}>
                        <ListItemText primary={`Total : ${cart.finalPrice}€ T.T.C`} />
                    </ListItem>
                    <ListItem style={{ justifyContent: "center" }}>
                        <Typography variant="body2" component="span" style={{ fontStyle: "italic" }}>
                            Nous ne stockons pas vos données bancaires.
                        </Typography>
                    </ListItem>
                    <ListItem style={{ justifyContent: "center" }}>
                        <TextField
                            className={classes.commentField}
                            id="comment-textfield"
                            label="Veuillez indiquer vos commentaires ou demandes spécifiques (taille, etc)"
                            multiline
                            rows={2}
                            rowsMax={4}
                            value={comment}
                            onChange={(event) => handleComment(event.target.value)}
                        />
                    </ListItem>
                </List>
            </Dialog>
        </div>
    );
}

ProductDetail.propTypes = {
    counters: PropTypes.object.isRequired,
    shipping: PropTypes.object.isRequired,
    email: PropTypes.object.isRequired,
    products: PropTypes.object.isRequired,
    handleClose: PropTypes.func.isRequired,
    handleShipping: PropTypes.func.isRequired,
    handleComment: PropTypes.func.isRequired,
    onChange: PropTypes.func.isRequired,
    handleCheckout: PropTypes.func.isRequired,
};

export default ProductDetail;
