import React, { Component } from "react";

import readingTime from "reading-time";

import { MuiThemeProvider } from "@material-ui/core/styles";

import { CssBaseline, Button, Snackbar } from "@material-ui/core";

import { auth, firestore } from "../../firebase";
import authentication from "../../services/authentication";
import appearance from "../../services/appearance";

import Loader from "../Loader";
import Bar from "../Bar";
import Router from "../Router";
import DialogHost from "../DialogHost";

const initialState = {
  ready: false,
  performingAction: false,
  theme: appearance.defaultTheme,
  user: null,
  userData: null,
  roles: [],

  connectDialog: {
    open: false,
  },

  settingsDialog: {
    open: false,
  },

  signOutDialog: {
    open: false,
  },

  snackbar: {
    autoHideDuration: 0,
    message: "",
    open: false,
  },
};

class App extends Component {
  constructor(props) {
    super(props);

    this.state = initialState;
  }

  resetState = (callback) => {
    this.setState(
      {
        ready: true,
        theme: appearance.defaultTheme,
        user: null,
        userData: null,
        roles: [],
      },
      callback
    );
  };

  setTheme = (theme, callback) => {
    if (!theme) {
      this.setState(
        {
          theme: appearance.defaultTheme,
        },
        callback
      );

      return;
    }

    this.setState(
      {
        theme: appearance.createTheme(theme),
      },
      callback
    );
  };

  openDialog = (dialogId, callback) => {
    const dialog = this.state[dialogId];

    if (!dialog || dialog.open === undefined || null) {
      return;
    }

    dialog.open = true;

    this.setState({ dialog }, callback);
  };

  closeDialog = (dialogId, callback) => {
    const dialog = this.state[dialogId];

    if (!dialog || dialog.open === undefined || null) {
      return;
    }

    dialog.open = false;

    this.setState({ dialog }, callback);
  };

  closeAllDialogs = (callback) => {
    this.setState(
      {
        connectDialog: {
          open: false,
        },

        settingsDialog: {
          open: false,
        },

        signOutDialog: {
          open: false,
        },
      },
      callback
    );
  };


  signOut = () => {
    this.setState(
      {
        performingAction: true,
      },
      () => {
        authentication
          .signOut()
          .then(() => {
            this.closeAllDialogs(() => {
              this.openSnackbar("Vous êtes déconnecté");
            });
          })
          .catch((reason) => {
            const code = reason.code;
            const message = reason.message;

            switch (code) {
              default:
                this.openSnackbar(message);
                return;
            }
          })
          .finally(() => {
            this.setState({
              performingAction: false,
            });
          });
      }
    );
  };

  openSnackbar = (message, autoHideDuration = 2, callback) => {
    this.setState(
      {
        snackbar: {
          autoHideDuration: readingTime(message).time * autoHideDuration,
          message,
          open: true,
        },
      },
      () => {
        if (callback && typeof callback === "function") {
          callback();
        }
      }
    );
  };

  closeSnackbar = (clearMessage = false) => {
    const { snackbar } = this.state;

    this.setState({
      snackbar: {
        message: clearMessage ? "" : snackbar.message,
        open: false,
      },
    });
  };

  render() {
    const {
      ready,
      performingAction,
      theme,
      user,
      userData,
      roles,
    } = this.state;

    const {
      connectDialog,
      settingsDialog,
      signOutDialog,
    } = this.state;

    const { snackbar } = this.state;

    return (
      <MuiThemeProvider theme={theme}>
        <CssBaseline />

        {!ready && <Loader />}

        {ready && (
          <>
            <Router
              user={user}
              roles={roles}
              bar={
                <Bar
                  performingAction={performingAction}
                  theme={theme}
                  user={user}
                  userData={userData}
                  roles={roles}
                  onConnectClick={() => this.openDialog("connectDialog")}
                  onSettingsClick={() => this.openDialog("settingsDialog")}
                  onSignOutClick={() => this.openDialog("signOutDialog")}
                />
              }
              openSnackbar={this.openSnackbar}
              onSettingsClick={() => this.openDialog("settingsDialog")}
              onConnectClick={() => this.openDialog("connectDialog")}
            />

            <DialogHost
              performingAction={performingAction}
              theme={theme}
              user={user}
              userData={userData}
              openSnackbar={this.openSnackbar}
              dialogs={{

                connectDialog: {
                  dialogProps: {
                    open: connectDialog.open,

                    onClose: (callback) => {
                      this.closeDialog("connectDialog");

                      if (callback && typeof callback === "function") {
                        callback();
                      }
                    },
                  },
                },

                settingsDialog: {
                  dialogProps: {
                    open: settingsDialog.open,

                    onClose: () => this.closeDialog("settingsDialog"),
                  },
                },

                signOutDialog: {
                  dialogProps: {
                    open: signOutDialog.open,

                    onClose: () => this.closeDialog("signOutDialog"),
                  },

                  props: {
                    title: "Déconnexion",
                    contentText:
                      "Une fois déconnecté, vous ne pourrez pas consulter vos commandes et modifier vos informations.",
                    dismissiveAction: (
                      <Button
                        color="primary"
                        onClick={() => this.closeDialog("signOutDialog")}
                      >
                        Annuler
                      </Button>
                    ),
                    confirmingAction: (
                      <Button
                        color="primary"
                        disabled={performingAction}
                        variant="contained"
                        onClick={this.signOut}
                      >
                        Se déconnecter
                      </Button>
                    ),
                  },
                },
              }}
            />

            <Snackbar
              autoHideDuration={snackbar.autoHideDuration}
              message={snackbar.message}
              open={snackbar.open}
              onClose={this.closeSnackbar}
            />
          </>
        )}
      </MuiThemeProvider>
    );
  }

  componentDidMount() {
    this.onAuthStateChangedObserver = auth.onAuthStateChanged(
      (user) => {
        // The user is not signed in or doesn’t have a user ID.
        if (!user || !user.uid) {
          if (this.userDocumentSnapshotListener) {
            this.userDocumentSnapshotListener();
          }

          this.resetState();

          return;
        }

        // The user is signed in, begin retrieval of external user data.
        this.userDocumentSnapshotListener = firestore
          .collection("users")
          .doc(user.uid)
          .onSnapshot(
            (snapshot) => {
              const data = snapshot.data();

              // The user doesn’t have a data point, equivalent to not signed in.
              if (!snapshot.exists || !data) {
                if (this.userDocumentSnapshotListener) {
                  this.userDocumentSnapshotListener();
                }

                this.resetState();

                return;
              }

              authentication
                .getRoles()
                .then((value) => {
                  this.setState({
                    ready: true,
                    user: user,
                    userData: data,
                  });
                })
                .catch((reason) => {
                  this.resetState(() => {
                    const code = reason.code;
                    const message = reason.message;

                    switch (code) {
                      default:
                        this.openSnackbar(message);
                        return;
                    }
                  });
                });
            },
            (error) => {
              this.resetState(() => {
                const code = error.code;
                const message = error.message;

                switch (code) {
                  default:
                    this.openSnackbar(message);
                    return;
                }
              });
            }
          );
      },
      (error) => {
        this.resetState(() => {
          const code = error.code;
          const message = error.message;

          switch (code) {
            default:
              this.openSnackbar(message);
              return;
          }
        });
      }
    );
  }

  componentWillUnmount() {
    if (this.onAuthStateChangedObserver) {
      this.onAuthStateChangedObserver();
    }

    if (this.userDocumentSnapshotListener) {
      this.userDocumentSnapshotListener();
    }
  }
}

export default App;
