import React, { Component } from "react";

import PropTypes from "prop-types";

import { HashRouter, Switch, Redirect, Route } from "react-router-dom";

import HomePage from "../HomePage";
import GuidePratiquePage from "../GuidePratiquePage";
import SoutiensPage from "../SoutiensPage";
import RessourcesPage from "../RessourcesPage";
import ContactPage from "../ContactPage";
import ProductPage from "../ProductPage";
import OrderPage from "../OrderPage";
import MentionsLegalesPage from "../MentionsLegalesPage";
import FormulaireSoutienPage from "../FormulaireSoutienPage";
import MerciPage from "../MerciPage";
import CgvPage from "../CgvPage";

class Router extends Component {
  render() {
    // Properties
    const { user, bar } = this.props;

    // Functions
    const { openSnackbar, onConnectClick, onSettingsClick } = this.props;

    return (
      <HashRouter>
        {bar}

        <Switch>
          <Route path="/" exact>
            <HomePage />
          </Route>

          <Route path="/produits">
            <ProductPage user={user} onConnectClick={onConnectClick} openSnackbar={openSnackbar} />
          </Route>

          <Route path="/guide-pratique">
            <GuidePratiquePage />
          </Route>

          <Route path="/soutiens">
            <SoutiensPage />
          </Route>

          <Route path="/formulaire-soutien">
            <FormulaireSoutienPage />
          </Route>

          <Route path="/merci">
            <MerciPage />
          </Route>

          <Route path="/ressources">
            <RessourcesPage />
          </Route>

          <Route path="/contact">
            <ContactPage />
          </Route>

          <Route path="/mentions-legales">
            <MentionsLegalesPage />
          </Route>

          <Route path="/conditions-vente">
            <CgvPage />
          </Route>

          <Route path="/commandes">
            <OrderPage user={user} onSettingsClick={onSettingsClick} openSnackbar={openSnackbar} />
          </Route>

          <Route>
            <Redirect to={{ pathname: "/" }} />
          </Route>
        </Switch>
      </HashRouter>
    );
  }
}

Router.propTypes = {
  // Properties
  user: PropTypes.object,
  roles: PropTypes.array.isRequired,
  bar: PropTypes.element,
  footer: PropTypes.element,

  // Functions
  openSnackbar: PropTypes.func.isRequired,
};

export default Router;
